// import VTextField from "vuetify/lib/components/VTextField";
// import VTextarea from "vuetify/lib/components/VTextarea";
// import VSelect from "vuetify/lib/components/VSelect";
import { mapGetters, mapActions } from "vuex";
let basePath = window.location.origin;
export default {
  components: {},
  props: ["header_id", "form_id"],
  data: () => ({
    name: "Form2Modal",
    indicator2: [
      {
        radio1: null,
        radio2: null,
        radio3: null,
        radio4: null,
        radio5: null,
        radio6: null,
        radio7: null,
        remarks1: null,
        remarks2: null,
        remarks3: null,
        remarks4: null,
        capacity: null,
        location: null,
        fdLocation: null,
        lifeSpan: null,
        fdStatus: null,
