        radio1: null,
        remarks1: null,
      },
      {
        radio1: null,
        remarks1: null,
      },
    ],
  }),
  created() {
    if (this.form_id != undefined) {
      this.fetchForm2({
        id: this.form_id,
      }).then(() => {
        this.setForm2Details2();
        this.dialogPrint = true;
      });
    } else {
      this.fetchRegForm2({
        id: this.header_id,
      }).then(() => {
        this.setForm2Details2();
        this.dialogPrint = true;
        let regEval = JSON.stringify(this.allForm2);
        this.$emit("update", JSON.parse(regEval));
      });
    }
  },
  methods: {
    ...mapActions(["fetchRegForm2", "fetchForm2"]),
    setForm2Details2() {
      let regEval = JSON.stringify(this.allForm2);
      let new_data = JSON.parse(regEval);
      if (new_data != null) {
        this.indicator2[0].radio1 = new_data.indicator_com_1_1;
        this.indicator2[0].radio2 = new_data.indicator_com_1_2;
        this.indicator2[0].radio3 = new_data.indicator_com_1_3;
        this.indicator2[0].radio4 = new_data.memo_agreement;
        this.indicator2[0].radio5 = new_data.contract;
        this.indicator2[0].radio6 = new_data.indicator_com_1_4;
        this.indicator2[0].radio7 = new_data.disposal_area;
        this.indicator2[0].remarks1 = new_data.indicator_rem_1_1;
        this.indicator2[0].remarks2 = new_data.indicator_rem_1_2;
        this.indicator2[0].remarks3 = new_data.indicator_rem_1_3;
        this.indicator2[0].remarks4 = new_data.indicator_rem_1_4;

        this.indicator2[0].capacity = new_data.capacity;
        this.indicator2[0].location = new_data.location;
        this.indicator2[0].fdLocation = new_data.fdLocation;
        this.indicator2[0].lifeSpan = new_data.lifeSpan;
        this.indicator2[0].fdStatus = new_data.fdStatus;

        this.indicator2[1].radio1 = new_data.indicator_com_2_1;
        this.indicator2[1].remarks1 = new_data.indicator_rem_2_1;

        this.indicator2[2].radio1 = new_data.indicator_com_3_1;
        this.indicator2[2].radio2 = new_data.indicator_com_3_1;
        this.indicator2[2].remarks1 = new_data.indicator_rem_3_1;

        this.indicator2[3].radio1 = new_data.indicator_com_4_1;
        this.indicator2[3].remarks1 = new_data.indicator_rem_4_1;

        this.indicator2[4].radio1 = new_data.indicator_com_5_1;
        this.indicator2[4].radio2 = new_data.indicator_com_5_2;
        this.indicator2[4].remarks1 = new_data.indicator_rem_5_1;
        this.indicator2[4].remarks2 = new_data.indicator_rem_5_2;

        this.indicator2[5].radio1 = new_data.indicator_com_6_1;
        this.indicator2[5].remarks1 = new_data.indicator_rem_6_1;

        this.indicator2[6].radio1 = new_data.indicator_com_7_1;
        this.indicator2[6].remarks1 = new_data.indicator_rem_7_1;
