<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  props: ["header_id"],
  data: () => ({
    name: "HistoryViewModal",

    items: [
      {
        color: "red",
        icon: "mdi-star",
        year: "1960",
      },
      {
        color: "purple",
        icon: "mdi-book-variant",
        year: "1970",
      },
      {
        color: "green",
        icon: "mdi-airballoon",
        year: "1980",
      },
      {
        color: "indigo",
        icon: "mdi-buffer",
        year: "1990",
      },
    ],
    monthNames: [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ],
    newHistoryData: [],
    //url: window.location.origin,
    url: window.location.origin,
  }),
  created() {
    this.fetchHistory({
      header_id: this.header_id,
    }).then(() => {
      this.fetchAllTypAttach({
        header_id: this.header_id,
      }).then(() => {
        this.newHistoryData = this.allHistory[0];
        this.setAttachment();
      });
    });
  },
  methods: {
    ...mapActions(["fetchHistory", "fetchAllTypAttach"]),
    setColor2(res) {
      if (res == "Submit") {
        return "green";
      } else if (res == "Forward") {
        return "blue";
      } else if (res == "Return") {
        return "red";
      } else if (res == "Assign") {
        return "deep-orange";
      }
    },
    setColor(res) {
      if (res == "1") {
        return "green";
      } else if (res == "2") {
        return "blue";
      } else if (res == "3") {
        return "teal  ";
      } else if (res == "4") {
        return "brown";
      } else if (res == "5") {
        return "brown";
      }
    },
    convertDate(date) {
      const date2 = new Date(date);
      //const month = date.toLocaleString("default", { month: "long" });
      //return date.getMonth(long);
      return (
        this.monthNames[date2.getMonth()] +
        " " +
        date2.getDate() +
        ", " +
        date2.getFullYear()
      );
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    setAttachment() {
      let newAllAttach = JSON.parse(JSON.stringify(this.allTypAttach));
      let count = this.newHistoryData.length;

      for (let i = 0; i < count; i++) {
        this.newHistoryData[i] = [this.newHistoryData[i]].map((e, i) => ({
          ...e,
          files: newAllAttach
            .filter((e2) => e.route_order === e2.route)
            .map((e, i) => ({
              ...e,
              file_path_name: JSON.parse(e.file_path_name).map((e3, i) => ({
                ...e3,
                new_name: `${this.url}/swms_api/public/typ_file/${e.reference_number}/others/${e3.new_name}`,
              })),
            })),
        }));
      }

      console.log(this.newHistoryData);
    },
  },
  computed: { ...mapGetters(["allHistory", "allTypAttach"]) },
};
</script>