      //var chapterBase = chapter - 1;
      if (new_data) {
        let count = new_data.length;

        for (let i = 0; i < count; i++) {
          let [b] = [new_data[i]].map((e, i) => ({
            ...e,
            file_path_name: JSON.parse(e.file_path_name).map((e, i) => ({
              ...e,
              new_name: `${basePath}/swms_api/public/typ_file/${reference_number}/outline/${e.new_name}`,
              // new_name: `${this.url}/swms_api/public/typ_file/${reference_number}/outline/`,
              ext: e.new_name.split(".").pop().toLowerCase(),
            })),
          }));
          this.chapters[new_data[i].chapter - 1].outline[
            new_data[i].outline
          ].uploads[0].curr_data = b.file_path_name;

          console.log(
            this.chapters[new_data[i].chapter - 1].outline[new_data[i].outline]
              .uploads[0].curr_data
